/* Scss Document */
.detail-live{
    .header-content,
    .title-detail,
    .description{
        padding: 0 20px;
    }
    .related-list{padding-left: 20px;}
    &.page-detail.top-detail{
        // .sidebar-2{
        //     max-width: 390px;
        //     padding-left: 25px;
        // }
        .sidebar-1{
            max-width: calc(100% - 360px);
        }
    }
    &.detail-live-tongthuat.page-detail.top-detail{
        .sidebar-2{
            max-width: 365px;
            padding-left: 65px;
        }
        .sidebar-1{
            max-width: calc(100% - 365px);
        }
         
    }
    .fck_detail{
        padding:25px;
        background: rgba(255,255,255,0.7);
        border: 1px solid var(--border);
        margin-bottom: 20px;
        .tplCaption{
            
            .Image{
                background: #fff;
                text-align: left;
            }
        }
        .wrap-img-content{
            @extend %flexbox;
            justify-content: center;            
        }
    }
    .author{
        clear: both;
        line-height: 160%;
        margin-bottom: 9px;
    }
    .header-live{
        height: 32px;
        line-height: 32px;
        padding: 0 20px;
        font-size: 14px;
        margin-bottom: 18px;
        .ic-live:before{
            top: 13px;margin-right: 7px;
        }
        .time{
            display: inline-block;
            margin-left: 16px;
            position: relative;
            &:before{
                content: '';
                width: 1px;
                height: 14px;
                background: var(--border);
                position: absolute;
                left: -11px;
                top: 10px;
            }
            &.finish{
                margin-left: 0;
                &:before{
                    content: none;
                }
            }
        }
        .social{
            float: right;
            @extend %flexbox;
            .circle_s{
                width: 32px;
                height: 32px;
                border-radius: 50%;
                border: 1px solid var(--border);
                margin-left: 10px;
                justify-content: center;
                .icon-svg{align-self: center;}
                &.fb{
                    &:hover{
                        .icon-svg{fill: #3B5999;}
                    }
                    
                }

                &.tw{
                    &:hover{
                        .icon-svg{fill: #55ACEE;}
                    }
                    
                }

                &.luutin{
                    &:hover{
                        .icon-svg{fill: #076DB6;}
                    }
                    
                }
                &.luutin.active{
                    &:hover{
                        .icon-svg{fill: #fff;}
                    }
                    
                }
            }
            .count_com_hoz{align-items: center;
                .number_cmt{padding-left: 5px; font-size: 12px;color: #757575;}
                &:hover{
                    .icon-svg{fill: #9F224E;}
                }
            }
        }
    &.endgame{
        .time{margin-left: 0;
            &:before{display: none;}
        }
    }
    }
    .tab-live{
        background: var(--bg-agray);
        padding: 20px 25px;
        border: 1px solid var(--border);
        border-bottom:0;
        border-radius: 4px 4px 0px 0px;
        font: 400 18px var(--font2);
        a{
            position: relative;
            margin-right: 20px;
            padding-bottom: 6px;
            display: inline-block;
            color: var(--gray1);font-weight: 300;
            @include cubic-transition($delay: 0, $duration: 200ms, $property: (all));
            &.active{
                color: var(--colormain);
                font-weight: bold;
            }
            &:hover{
                color: var(--colormain);
            }
        }
    }
    .wrap-sort{
        text-align: right;
        font-size: 0;
        margin-bottom: 10px;
        a{
            width: 100px;
            height: 36px;
            line-height: 36px;
            text-align: center;
            background: #7043A9;
            text-decoration: none;
            display: inline-block;
            color: #fff;
            font-size: 14px;
            border: 1px solid #7043A9;
            @include cubic-transition($delay: 0, $duration: 200ms, $property: (all));
            &:first-child{
                border-radius: 3px 0px 0px 3px;
            }   
            &:last-child{
                border-radius: 0px 3px 3px 0px;
            }
            &:before{content: none;}
            
            &:hover{
                background: #bdbdbd;
                border: 1px solid #bdbdbd;
            }
            &.active{
                background: #fff;border: 1px solid  var(--border);
                color: var(--colormain);
            }
        }
    }
    .timeline{
        display: inline-block;
        font-size: 14px;
        line-height: 16px;
        color: var(--gray1);
        margin-bottom: 10px;
        strong{
            display: inline-block;
            margin-left: 20px;
            position: relative;
            color: #222;
            &:before{
                content: '';
                width: 1px;
                height: 10px;
                background: #E0E0E0;
                position: absolute;
                left: -11px;
                top: 3px;
            }
        }
    }
    .list-live{
        
    }
    .block-item{
        width: 100%;
        float: left;
        padding-bottom: 15px;
        margin-bottom: 15px;
        border-bottom: 1px solid var(--border);        
        &:last-of-type{
            border-bottom: none;
            margin-bottom: 0; padding-bottom: 0;
        }
    }
    .title-block-live{
        font: bold 20px/1.4 var(--font2);
        margin-bottom: 15px;
    }
    .social-block{
        float: left;
        line-height: normal; align-items: center; opacity: 0; @include cubic-transition($delay: 0, $duration: 200ms, $property: (all)); 
        .txt-share{font-size: 13px; color: #757575; margin-right: 13px;}
        a{
            
            margin-right: 10px;
            padding-right: 10px;
            border-right: 1px solid #e5e5e5;
            text-decoration: none;
            &:last-of-type{border-right: none;}
            &:before{
                content: none;
            }
            justify-content: center;
            color: #076DB6; font-size: 13px;
            .ic{align-self: center; margin-right: 5px; width: 13px;height: 13px;}
            &.fb{
                .ic{fill: #3B5999;}
                &:hover{
                    .ic{fill: #3B5999;}
                }
            }
            &.tw{
                .ic{fill: #55ACEE;}
                &:hover{
                    .ic{fill: #55ACEE;}
                }
            }
        }
    }
    .block-pin{
        padding: 24px 24px 0 24px;
        background: #fff;
        margin-bottom: 20px;
        border: 1px solid var(--border);
        .Image{
            text-align: center;color: var(--gray1);font-size: 14px;padding-top: 15px;
        }
        p{  
            font: 400 18px arial;
            margin: 15px 0 0 0;
            line-height: 160%;
            
        }
        .social-block{margin: 20px 0;}
        .header-block{
            background: var(--colormain);
            margin: -24px -24px 0 -24px;
            padding: 12.5px 24px;
            p{
                font: bold 18px var(--font1);margin: 0;color: #fff;
            }
            .icon-svg{
                position: absolute;
                right: 24px;
                top: 15px;
                color: #fff;
            }
        }
    }
    &.detail-interview{
        .block-pin{
            .header-block{background: #fff;padding:28px 24px;
                p{
                    font: bold 18px var(--font2);margin: 0;color: var(--colormain);
                }
                .icon-svg{color: var(--gray3);top: 32px;}
            }
        }
    }
    .block-item, .block-pin{
        &:hover{
            .social-block{opacity: 1;}
        }
    }
    .score-table{
        @extend %flexbox;
        background: #2A0845;
        background: linear-gradient(143.05deg, #6441A5 0.53%, #2A0845 81.33%);
        padding: 20px 25px;
        color: #fff;
        font-family: var(--font1);
        margin-bottom: 48px;
        .group{
            width: 35%;
            @extend %flexbox;
            &:first-child{
                padding-right: 30px;
                strong{
                    margin-left: auto;
                }
            }
            &:last-child{
                flex-direction: row-reverse;
                padding-left: 30px;
                text-align: right;
                strong{
                    margin-right: auto;
                }
            }
            
            &.group-score{width: 30%; flex-wrap: wrap; align-self: center;
                .main-score{
                    width: 100%;
                    font-weight: bold;
                    font-size: 64px;
                    text-align: center;
                    line-height: 70px;
                    font-family: Arial;
                    span{color: rgba(255,255,255,0.5);
                        &.team-win{color: rgba(255,255,255,1);}
                    }
                    
                }
                .sub-score{width: 100%; font: 400 15px/140% arial; padding: 0 32px; margin-top: 15px;}
            }
        }
        .name{
            font: bold 24px/1.4 var(--font2);
            margin-bottom: 25px;
        }
        p{
            font-family: arial;
            font-size: 14px;
            margin-bottom: 8px;
        }
               
    }
    .btn-view{
        display: block;
        height: 48px;
        line-height: 48px;
        text-align: center;
        color: #fff;
        background: #0084FF;
        border-radius: 3px;
        font-size: 15px; text-decoration: none;
        clear: both;
        &:before{
            content: none;
        }
    &:hover{color: #fff;}
    }
    .wrap-notifile{
        width: 100%;
        float: left;
        @extend %flexbox;
        justify-content: center;
    }
    .notifile{
        height: 36px;
        line-height: 36px;
        box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
        border-radius: 18px;
        color: #fff;
        font-size: 15px;
        padding: 0 15px;
        display: inline-block;
        &.new-content{
            background: #04416D;
            .icon-svg,.ic{fill: #fff;}
        }
        &.new-goal{
            background: $vne_color;
        }
    }
    .wrap-notifile-backtotop{
        .notifile{
            height: 40px; width: 40px; line-height: 40px;border-radius: 50%; text-align: center; padding: 0; background: #04416D;
            .icon-svg,.ic{fill: #fff;}
        }
    }
    &.page-detail{
        .tags{border-bottom: none;}
    }
}
.detail-live .wrap-notifileback {
    justify-content: unset;
    position: fixed;
    top: 120px;
    left: calc(50% - 205px);
    z-index: 99 !important;
    cursor: pointer;
}
.detail-live .wrap-notifileback {
    width: 100%;
    float: left;
    justify-content: center;
}
.header-block{position: relative;
    .ic-pin{position: absolute; top: 5px; right: 5px;}
}
.endgame .ic-live{display:none}.detail-live .header-live.endgame .time{margin-left:0 !important}.detail-live .header-live.endgame .time:before{display:none !important}
/*tab-comment*/
.tab-comment{
    background: #fff;
    border: 1px solid var(--border);
    .header-tab{
        @extend %flexbox;
        font: 600 16px var(--font1);
        a{
            width: 50%;
            height: 48px;
            line-height: 48px;
            text-align: center;
            background:var(--white);
            color: var(--colormain);
            &.active{
                color: var(--white);
                background: var(--colormain);
            }
        }
    }
    .content-tab{
        padding: 20px 0 20px 10px;        
        .start{
            font-size: 15px;
            font-weight: bold;
            line-height: 23px;
            color: #757575;
            margin-bottom: 15px;
            padding-left: 25px;
            position: relative;
            width: 100%; float: left;
            &:before{
                content: '';
                width: 9px;
                height: 1px;
                background: var(--border);
                position: absolute;
                left: 1px;
                top: 13px;
            }
            &.updating{
                color: $vne_color;
                &:before{
                    height: 9px;
                    border-radius: 50%;
                    background: $vne_color;
                    top: 8px;
                    z-index: 1;
                }
            }
        }
        .ds-dienbien{
            padding: 0 20px 0 25px;
            position: relative;
            &:before{
                content: '';
                width: 1px;
                height: calc(100% + 30px);
                position: absolute;
                left: 5px;
                top: -25px;
                background: var(--border);
            }
            li{
                position: relative;
                margin-bottom: 15px;
                strong{
                    font-size: 15px;
                    line-height: 16px;
                    font-weight: bold;
                }
                a{
                    display: block;
                    font-size: 15px;
                    line-height: 23px;
                    @include cubic-transition($delay: 0, $duration: 200ms, $property: (all));
                    &:hover{
                        color: #076DB6;
                    }
                }
                &.time{
                    color: #757575;
                    font-size: 14px;
                    line-height: 16px;
                    &:before{
                        content: none;
                    }
                }
                &:before{
                    content: '';
                    width: 9px;
                    height: 9px;
                    border-radius: 50%;
                    background: var(--border);
                    position: absolute;
                    left: -24px;
                    top: 4px;
                }
                &.active{
                    &:before{
                        background: #757575;
                    }
                }
            }
        }
        .scrollbar-right{
            height: 610px; overflow: hidden;
        }
        .box_comment_pin{
            padding: 0 10px 0 0px;
            margin-bottom: 0!important;
        }
    }
}
/*End tab-comment*/
