    .wrap-notifile-backtotop{top: 120px; left: calc(50% - 205px);}
}

.fck_detail_pin {
	border: none !important;
	padding: 0 !important;
    margin-bottom: 0 !important;
    .header-block + p{padding-right: 30px;}
}

.fck_detail_pin .desc_cation {
	padding: 0 10px !important;
}

.header-block.date-line span {
	color: #9f224e;
	font-weight: 700;
}

.detail-live .block-item:last-child {
	margin-bottom: 15px;
}

.detail-live .block-item.last {
	border-bottom: none;
	margin-bottom: 0;
}

.spinner2 {
	margin: 0 auto;
	width: 70px;
	text-align: center;
}

.spinner2>div {
	width: 10px;
	height: 10px;
	background-color: #9f224e;
	border-radius: 100%;
	display: inline-block;
	-webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
	animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner2 .bounce1 {
	-webkit-animation-delay: -0.32s;
	animation-delay: -0.32s;
}

.spinner2 .bounce2 {
	-webkit-animation-delay: -0.16s;
	animation-delay: -0.16s;
}

.ds-dienbien li strong, .related-list li {
    position: relative;
}

.ds-dienbien li strong .spinner2 {
    position: absolute;
    top: 1px;
    left: -55px;
}

.related-list li .spinner2 {
    position: absolute;
    top: 0;
    left: -33px;
}

@-webkit-keyframes sk-bouncedelay {
	0%,
	80%,
	100% {
		-webkit-transform: scale(0)
	}
	40% {
		-webkit-transform: scale(1.0)
	}
}

@keyframes sk-bouncedelay {
	0%,
	80%,
	100% {
		-webkit-transform: scale(0);
		transform: scale(0);
	}
	40% {
		-webkit-transform: scale(1.0);
		transform: scale(1.0);
	}
}
.content-block .description .meta-news {
    display: none;
}
.ic-pin.active {
    fill: #076DB6;
}
.ic-pin {
    cursor: pointer;
}
