    .box_embed_video_parent{
        overflow: hidden;
        max-width: calc(100% + 48px);
        width: calc(100% + 48px);
        margin: 0 -24px 0;
        .embed-container{margin-bottom: 0;}
    }
}
.box_embed_video {
    width: 100%;
    position: relative;
    background: #e1e1e1
}
.box_embed_video .embed-container,.box_embed_video .embed-container-square {
    transition-duration: 300ms;
    transition-property: left;
    transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}
