.box_embed_video .parser_title {
    opacity: 0;
    visibility: hidden;
    width: 100%;
    position: absolute;
    top: 180px;
    left: 0;
    background: #fff;
    padding: 10px;
    font: 400 14px arial;
    color: #333;
    transition-duration: 200ms;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.2, 1, 0.2, 1);
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: left;
    height: auto;
}
.box_embed_video .embed-container .parser_title b {
    color: #9F224E
}
.box_embed_video .close-video{
    color: #333; font-family: -webkit-body; font-size: 33px; width: 30px; height: 30px; line-height: 28px; position: absolute; top: -15px; right: -15px; cursor: pointer; text-align: center; display: none;
    background: #fff;
    border: 1px solid var(--border);
    border-radius: 50%;
}

.start_sticky .embed-container.fadeout, .start_sticky .embed-container-square.fadeout {
    opacity: 0
}
.start_sticky .embed-container.fadein, .start_sticky .embed-container-square.fadein {
    opacity: 1;
    -webkit-transition: opacity 0.5s;
    transition: opacity 0.5s;
    -webkit-animation: animationVideoUp .5s ease;
    animation: animationVideoUp .5s ease
}

.start_sticky .embed-container.fadeinDown, .start_sticky .embed-container-square.fadeinDown {
    opacity: 1;
    -webkit-transition: opacity 0.5s;
    transition: opacity 0.5s;
    bottom: auto;
    top: 70px;
    -webkit-animation: animationVideoDown .5s ease;
    animation: animationVideoDown .5s ease
}


.box_embed_video .embed-container.fadeout-up,.box_embed_video .embed-container-square.fadeout-up {
    opacity: 0
}
.box_embed_video .embed-container.fadein-up, .box_embed_video .embed-container-square.fadein-up {
    opacity: 1;
    -webkit-transition: opacity 1s;
    transition: opacity 1s
}
.start_sticky .box_embed_video .embed-container .parser_title,.start_sticky .box_embed_video .embed-container-square .parser_title {
    opacity: 1;
    visibility: visible
}

